.note {
  border: 1px tomato dashed;
  cursor: pointer; }
  .note:hover {
    background-color: gold; }
  .note.active {
    background-color: lightskyblue; }

.note-card {
  position: fixed;
  width: 280px;
  bottom: 1em;
  right: 1em;
  padding: 1em;
  display: block;
  border-radius: 4px;
  -webkit-transition: opacity 300ms ease, -webkit-transform 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: opacity 300ms ease, -webkit-transform 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  -o-transition: transform 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19), opacity 300ms ease;
  transition: transform 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19), opacity 300ms ease;
  transition: transform 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19), opacity 300ms ease, -webkit-transform 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
  -webkit-transform: translateY(20px);
      -ms-transform: translateY(20px);
          transform: translateY(20px);
  opacity: 0;
  color: white;
  background-color: #48505d;
  -webkit-box-shadow: 2px 3px 16px silver;
          box-shadow: 2px 3px 16px silver; }
  .note-card.active {
    opacity: 1;
    -webkit-transform: translateY(0px);
        -ms-transform: translateY(0px);
            transform: translateY(0px); }
  .note-card li {
    background-color: rgba(255, 255, 255, 0.1);
    padding: 4px;
    margin-bottom: 1em; }
    .note-card li > span {
      font-style: italic; }
    .note-card li p {
      margin: 0; }



